import axios from 'axios'

const req = axios.create({
    baseURL: 'http://127.0.0.1:9999/api',
    headers: {'Content-Type': 'application/json'}, 
})

// 文章相关接口
export const getEssay = (essay_id,cate_id) => {
    return req.post('/essay/get_essays',{
       essay_id,
       cate_id
    })
}


// 分类相关接口
export const get_cate = () => {
    return req.post('/essay/get_cates')
}