import {get_cate} from '../api/index'
export default{
  namespaced: true,
  actions:{
    get_menu(context, value){
      get_cate().then(response =>{
        context.state.menu_data = response.data.data
      }).catch( ()=> {
        if(response.data.code == 1){
           console.log('请求出错了')
        }
      })
    }
  },

    mutations:{
        
    },

    state:{
      menu_data:'这是一个初始值'
    },
}